import React, { useEffect, useState } from 'react';
import { fetchUsers, fetchActionHistory, fetchTasksForUser, addTaskForUser } from '../services/firebase';
import '../tailwind.css';
import '../index.css';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';

const tasksList = [
  '  ',
  '  ',
  '   ',
  '   ',
  '   ',
  ' ',
  ' ',
  ' ',
  '',
  '',
  '',
  ' ',
  ' ',
  ' '
];

const getTaskCellContent = (tasks = [], index) => {
  return tasks[index] || ' '; // Return task content or ' ' if no task exists
};

// Utility functions for formatting
const formatTime = (date) => {
  let hours = date.getHours(); //  
  const minutes = date.getMinutes();
  const seconds = date.getSeconds();
  const ampm = hours >= 12 ? 'PM' : 'AM';

  hours = hours % 12; //   
  hours = hours ? hours : 12; //   
  const formattedHours = hours < 10 ? `0${hours}` : hours;
  const formattedMinutes = minutes < 10 ? `0${minutes}` : minutes;
  const formattedSeconds = seconds < 10 ? `0${seconds}` : seconds;

  return `${formattedHours}:${formattedMinutes}:${formattedSeconds} ${ampm}`;
};

const formatDate = (date) => {
  const day = date.getDate(); //  
  const month = date.getMonth() + 1; //    
  const year = date.getFullYear();

  const formattedDay = day < 10 ? `0${day}` : day;
  const formattedMonth = month < 10 ? `0${month}` : month;

  return `${formattedDay}-${formattedMonth}-${year}`;
};


const AdminPage = () => {
  const [users, setUsers] = useState([]);
  const [selectedUser, setSelectedUser] = useState('');
  const [selectedDate, setSelectedDate] = useState('');
  const [userActions, setUserActions] = useState([]);
  const [userTasks, setUserTasks] = useState([]);
  const [tasksForAllUsers, setTasksForAllUsers] = useState({});
  const [selectedTasks, setSelectedTasks] = useState([]);
  const [loading, setLoading] = useState(false);

  // Load users and their tasks
  const loadUsersAndTasks = async () => {
    setLoading(true);
    try {
      const fetchedUsers = await fetchUsers();
      const filteredUsers = fetchedUsers.filter(user => user.id !== '1000'); // Exclude specific user ID
      
      setUsers(filteredUsers);

      const tasksData = {};
      for (const user of filteredUsers) {
        const tasks = await fetchTasksForUser(user.id);
        tasksData[user.id] = tasks || []; // Ensure tasksData[user.id] is always an array
      }
      setTasksForAllUsers(tasksData);
      if (selectedUser) {
        const tasks = await fetchTasksForUser(selectedUser);
        setUserTasks(tasks || []); // Ensure userTasks is always an array
      }
    } catch (error) {
      console.error('     :', error);
    } finally {
      setLoading(false);
    }
  };
  
  useEffect(() => {
    loadUsersAndTasks(); // Load users and their tasks on mount
  }, []);

  const handleUserChange = async (event) => {
    const userId = event.target.value;
    setSelectedUser(userId);
    if (userId) {
      try {
        const actions = await fetchActionHistory(userId);

        const sortedActions = actions.sort((a, b) => a.timestamp.toDate() - b.timestamp.toDate());
        
        setUserActions(sortedActions);
        const tasks = await fetchTasksForUser(userId);
        setUserTasks(tasks || []); // Ensure userTasks is always an array
        setSelectedTasks(tasks || []); // Ensure selectedTasks is always an array
      } catch (error) {
        console.error('    :', error);
      }
    } else {
      setUserActions([]);
      setUserTasks([]);
      setSelectedTasks([]);
    }
  };

  const handleDateChange = (event) => {
    setSelectedDate(event.target.value);
  };

  const handleSaveTasksForUser = async () => {
    if (selectedUser !== '') {
      try {
        await addTaskForUser(selectedUser, selectedTasks);
        toast.success('   ');
        await loadUsersAndTasks();
        const tasks = await fetchTasksForUser(selectedUser);
        setUserTasks(tasks || []); // Ensure userTasks is always an array
      } catch (error) {
        console.error('   :', error);
        toast.error('   ');
      }
    }
  };

  const handleTaskChange = (event) => {
    const value = event.target.value;
    setSelectedTasks((prev) => 
      prev.includes(value)
        ? prev.filter(task => task !== value)
        : [...prev, value]
    );
  };

  const onDragEnd = (result) => {
    if (!result.destination) return;

    const { source, destination } = result;
    const sourceId = source.droppableId;
    const destId = destination.droppableId;

    if (sourceId === 'allTasks' && destId === 'approvedTasks') {
      // Move task from 'All Tasks' to 'Approved Tasks'
      const task = tasksList[source.index];
      setSelectedTasks((prev) => [...prev, task]);
    } else if (sourceId === 'approvedTasks' && destId === 'allTasks') {
      // Move task from 'Approved Tasks' to 'All Tasks'
      const task = selectedTasks[source.index];
      setSelectedTasks((prev) => prev.filter(t => t !== task));
    }
  };

  // Determine the max number of tasks any user has
  const maxTasksLength = Math.max(...users.map(user => (tasksForAllUsers[user.id] || []).length), 0);

  return (
    <div className="min-h-screen flex flex-col items-center justify-center p-4 rtl">
      <ToastContainer />
      <div className="w-full max-w-2xl mx-auto">
        <h1 className="text-2xl font-bold mb-4 text-center"> </h1>
        <h2 className="text-lg font-semibold mb-4 text-center">     </h2>

        <select onChange={handleUserChange} value={selectedUser} className="mb-4 border border-gray-300 rounded p-2 w-full">
          <option value=""> </option>
          {users.map(user => (
            <option key={user.id} value={user.id}>
              {user.name} ({user.id})
            </option>
          ))}
        </select>

        {loading && <p className="text-center"> ...</p>}

        {selectedUser && (
          <div className="mt-4">
            <h3 className="text-lg font-semibold mb-2 text-center">   {selectedUser}</h3>

            <div className="mb-4">
              <label htmlFor="date" className="block text-sm font-medium mb-1"> : </label>
              <input
                type="date"
                id="date"
                value={selectedDate}
                onChange={handleDateChange}
                className="border border-gray-300 rounded p-2 w-full"
              />
            </div>

            <h4 className="text-lg font-semibold mb-2"> </h4>
            <ul className="list-disc pl-5">
              {userActions.length > 0 ? (
                userActions.map((action, index) => (
                  <li key={index} className="text-sm">
                    {action.actionType}  {formatDate(new Date(action.timestamp.toDate()))} {formatTime(new Date(action.timestamp.toDate()))}
                  </li>
                ))
              ) : (
                <li className="text-sm">    </li>
              )}
            </ul>

            <h4 className="text-lg font-semibold mb-2 mt-4"> </h4>

            <DragDropContext onDragEnd={onDragEnd}>
              <div className="flex gap-4 rtl">
                <Droppable droppableId="allTasks">
                  {(provided) => (
                    <div
                      ref={provided.innerRef}
                      {...provided.droppableProps}
                      className="flex-1 bg-gray-100 p-4 rounded-lg shadow-md"
                    >
                      <h5 className="text-lg font-semibold mb-2"> </h5>
                      {tasksList.map((task, index) => (
                        !selectedTasks.includes(task) && (
                          <Draggable key={task} draggableId={task} index={index}>
                            {(provided) => (
                              <div
                                ref={provided.innerRef}
                                {...provided.draggableProps}
                                {...provided.dragHandleProps}
                                className="p-3 bg-white border border-gray-300 rounded-lg mb-2 shadow-sm hover:bg-gray-50 rtl"
                              >
                                {task}
                              </div>
                            )}
                          </Draggable>
                        )
                      ))}
                      {provided.placeholder}
                    </div>
                  )}
                </Droppable>

                <Droppable droppableId="approvedTasks">
                  {(provided) => (
                    <div
                      ref={provided.innerRef}
                      {...provided.droppableProps}
                      className="flex-1 bg-gray-100 p-4 rounded-lg shadow-md"
                    >
                      <h5 className="text-lg font-semibold mb-2"> </h5>
                      {selectedTasks.map((task, index) => (
                        <Draggable key={task} draggableId={task} index={index}>
                          {(provided) => (
                            <div
                              ref={provided.innerRef}
                              {...provided.draggableProps}
                              {...provided.dragHandleProps}
                              className="p-3 bg-white border border-gray-300 rounded-lg mb-2 shadow-sm hover:bg-gray-50 rtl"
                            >
                              {task}
                            </div>
                          )}
                        </Draggable>
                      ))}
                      {provided.placeholder}
                    </div>
                  )}
                </Droppable>
              </div>
            </DragDropContext>

            <button onClick={handleSaveTasksForUser} className="mt-4 bg-blue-500 text-white py-2 px-4 rounded hover:bg-blue-600 transition">   </button>

            <h4 className="text-lg font-semibold mb-2 mt-6"> </h4>
            <div className="space-y-2">
              {userTasks.length > 0 ? (
                userTasks.map((task, index) => (
                  <div key={index} className="p-3 bg-white border border-gray-300 rounded-lg shadow-sm rtl">
                    {task}
                  </div>
                ))
              ) : (
                <div className="p-3 bg-white border border-gray-300 rounded-lg shadow-sm rtl">   </div>
              )}
            </div>
          </div>
        )}

        <div className="w-full mt-8 overflow-x-auto">
          <table className="min-w-full bg-white border border-gray-300 rounded-lg shadow-md rtl">
            <thead className="bg-gray-200">
              <tr>
                <th className="py-2 px-4 border-b text-right text-sm"></th>
                {Array.from({ length: maxTasksLength }, (_, index) => (
                  <th key={index} className="py-2 px-4 border-b text-right text-xs"> {index + 1}</th>
                ))}
              </tr>
            </thead>
            <tbody>
              {users.map((user) => (
                <tr key={user.id}>
                  <td className="py-2 px-4 border-b text-right text-sm">{user.name}</td>
                  {Array.from({ length: maxTasksLength }, (_, index) => (
                    <td key={index} className="py-2 px-4 border-b text-right text-xs">{getTaskCellContent(tasksForAllUsers[user.id], index)}</td>
                  ))}
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>
    </div>
  );
};

export default AdminPage;
