import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { fetchUsers } from '../services/firebase';

const LoginPage = () => {
  const [userId, setUserId] = useState('');
  const navigate = useNavigate();

  const handleLogin = async () => {
    try {
      const users = await fetchUsers();
      const user = users.find(u => u.id === userId);

      if (user) {
        if (user.id === '1000') {
          navigate('/admin');
        } else {
          navigate(`/user/${userId}`);
        }
      } else {
        alert('Invalid User ID');
      }
    } catch (error) {
      console.error('Error during login:', error);
      alert('An error occurred. Please try again.');
    }
  };

  return (
    <div className="flex items-center justify-center min-h-screen bg-gradient-to-r from-green-200 via-green-300 to-green-400">
      <div className="bg-white p-8 rounded-lg shadow-lg w-full max-w-md">
        <h1 className="text-2xl font-bold mb-4 text-center">Login Page</h1>
        <div className="mb-4">
          <input 
            type="text" 
            value={userId} 
            onChange={(e) => setUserId(e.target.value)} 
            placeholder="Enter User ID"
            className="w-full p-2 border border-gray-300 rounded-md"
          />
        </div>
        <div className="text-center">
          <button 
            onClick={handleLogin} 
            className="w-full bg-blue-500 text-white p-2 rounded-md hover:bg-blue-600"
          >
            Login
          </button>
        </div>
      </div>
    </div>
  );
};

export default LoginPage;
