import React, { useEffect, useState } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { logAction, fetchActionHistory, fetchTasksForUser, fetchUserDetails } from '../services/firebase';

const UserPage = () => {
  const { userId } = useParams();
  const navigate = useNavigate();
  const [actionType, setActionType] = useState('');
  const [actions, setActions] = useState([]);
  const [tasks, setTasks] = useState([]);
  const [userName, setUserName] = useState('');
  const [hasClockedIn, setHasClockedIn] = useState(false);
  const [selectedDate, setSelectedDate] = useState(new Date().toISOString().split('T')[0]); // Default to today

  const handleAction = async (type) => {
    setActionType(type);
    try {
      await logAction(userId, type);
      alert(`   ${type} `);
      loadActions();
    } catch (error) {
      console.error("   : ", error);
    }
  };

  const loadActions = async () => {
    try {
      const actions = await fetchActionHistory(userId);
      const filteredActions = actions
        .filter(action => {
          const actionDate = new Date(action.timestamp.toDate()).toISOString().split('T')[0];
          return actionDate === selectedDate;
        })
        .sort((a, b) => b.timestamp.toDate() - a.timestamp.toDate()); // Sort in descending order
      setActions(filteredActions);
      const clockInAction = filteredActions.find(action => action.actionType === 'Clock In');
      setHasClockedIn(!!clockInAction);
    } catch (error) {
      console.error("   : ", error);
    }
  };

  const loadTasks = async () => {
    try {
      const tasks = await fetchTasksForUser(userId);
      setTasks(tasks);
    } catch (error) {
      console.error("   : ", error);
    }
  };

  const loadUserDetails = async () => {
    try {
      const userDetails = await fetchUserDetails(userId);
      setUserName(userDetails.name || ''); // Handle case where name might be undefined
    } catch (error) {
      console.error("    : ", error);
    }
  };

  const handleTaskClick = () => {
    if (hasClockedIn) {
      navigate(`/mission/${userId}`);
    } else {
      alert('      .');
    }
  };

  const handleDateChange = (event) => {
    setSelectedDate(event.target.value);
  };

  useEffect(() => {
    loadActions();
  }, [selectedDate]);

  useEffect(() => {
    loadTasks();
    loadUserDetails();
  }, [userId]);

  return (
    <div className="flex flex-col items-center justify-center min-h-screen bg-gradient-to-r from-yellow-200 via-yellow-300 to-yellow-400 rtl">
      <div className="bg-white p-8 rounded-lg shadow-lg w-full max-w-lg text-center">
        <h1 className="text-3xl font-bold mb-6">  /   {userId || ''}</h1>
        
        <div className="flex flex-col items-center mb-6 space-y-2">
          <button 
            onClick={() => handleAction('Clock In')} 
            className="w-full bg-green-500 text-white px-4 py-2 rounded hover:bg-green-600"
          >
             
          </button>
          <button 
            onClick={() => handleAction('Clock Out')} 
            className="w-full bg-red-500 text-white px-4 py-2 rounded hover:bg-red-600"
          >
             
          </button>
          <button 
            onClick={() => handleAction('Break Start')} 
            className="w-full bg-yellow-500 text-white px-4 py-2 rounded hover:bg-yellow-600"
          >
             
          </button>
          <button 
            onClick={() => handleAction('Break End')} 
            className="w-full bg-blue-500 text-white px-4 py-2 rounded hover:bg-blue-600"
          >
              
          </button>
          <button 
            onClick={handleTaskClick} 
            className="w-full bg-purple-500 text-white px-4 py-2 rounded hover:bg-purple-600"
          >
            
          </button>
        </div>

        <div className="mb-6">
          <label htmlFor="date" className="mr-2"> :</label>
          <input
            type="date"
            id="date"
            value={selectedDate}
            onChange={handleDateChange}
            className="p-2 border border-gray-300 rounded"
          />
        </div>

        <div className="mb-6">
          <h2 className="text-xl font-semibold mb-2"> </h2>
          <ul>
            {actions.length > 0 ? (
              actions.map((action, index) => (
                <li key={index} className="mb-1">{action.actionType}  {new Date(action.timestamp.toDate()).toLocaleTimeString()}</li>
              ))
            ) : (
              <li>    </li>
            )}
          </ul>
        </div>

        <div>
          <h2 className="text-xl font-semibold mb-2"> </h2>
          <ul>
            {tasks.length > 0 ? (
              tasks.map((task, index) => (
                <li key={index} className="mb-1">{task}</li>
              ))
            ) : (
              <li>   </li>
            )}
          </ul>
        </div>
      </div>
    </div>
  );
};

export default UserPage;
