import React, { useState, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { addTaskForUser, fetchTasksForUser } from '../services/firebase';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';
import '../tailwind.css'; // Adjusted path for tailwind.css
import '../index.css'; // Adjusted path for index.css

const tasksList = [
  '  ',
  '  ',
  '   ',
  '   ',
  '   ',
  ' ',
  ' ',
  ' ',
  '',
  '',
  '',
  ' ',
  ' ',
  ' '
];

const Mission = () => {
  const { userId } = useParams();
  const navigate = useNavigate();
  const [allTasks, setAllTasks] = useState(tasksList);
  const [approvedTasks, setApprovedTasks] = useState([]);

  const handleSaveTasks = async () => {
    try {
      await addTaskForUser(userId, approvedTasks);
      toast.success('   '); // Show success toast
      navigate(`/user/${userId}`); // Navigate back to the user page
    } catch (error) {
      console.error("Error saving tasks: ", error);
      toast.error('   '); // Show error toast
    }
  };

  const handleOnDragEnd = (result) => {
    const { source, destination } = result;

    if (!destination) return;

    const sourceTasks = Array.from(source.droppableId === 'all-tasks' ? allTasks : approvedTasks);
    const [movedTask] = sourceTasks.splice(source.index, 1);
    const destinationTasks = Array.from(destination.droppableId === 'all-tasks' ? allTasks : approvedTasks);
    destinationTasks.splice(destination.index, 0, movedTask);

    if (source.droppableId === 'all-tasks') {
      setAllTasks(destination.droppableId === 'all-tasks' ? destinationTasks : sourceTasks);
    } else {
      setApprovedTasks(destination.droppableId === 'all-tasks' ? sourceTasks : destinationTasks);
    }
  };

  return (
    <div className="p-8 max-w-4xl mx-auto flex flex-col items-center rtl">
      <ToastContainer /> {/* Include ToastContainer to display toasts */}
      <h1 className="text-3xl font-bold mb-6 text-gray-800"> </h1>
      <DragDropContext onDragEnd={handleOnDragEnd}>
        <div className="flex w-full space-x-8">
          <Droppable droppableId="approved-tasks">
            {(provided) => (
              <div
                ref={provided.innerRef}
                {...provided.droppableProps}
                className="flex-1 bg-green-100 p-4 rounded-lg shadow-md"
              >
                <h2 className="text-2xl font-semibold mb-4 text-gray-800"> </h2>
                {approvedTasks.map((task, index) => (
                  <Draggable key={task} draggableId={task} index={index}>
                    {(provided) => (
                      <div
                        ref={provided.innerRef}
                        {...provided.draggableProps}
                        {...provided.dragHandleProps}
                        className="mb-2 p-4 bg-white rounded-lg shadow-sm border border-gray-200"
                      >
                        {task}
                      </div>
                    )}
                  </Draggable>
                ))}
                {provided.placeholder}
              </div>
            )}
          </Droppable>
          <Droppable droppableId="all-tasks">
            {(provided) => (
              <div
                ref={provided.innerRef}
                {...provided.droppableProps}
                className="flex-1 bg-blue-100 p-4 rounded-lg shadow-md"
              >
                <h2 className="text-2xl font-semibold mb-4 text-gray-800"> </h2>
                {allTasks.map((task, index) => (
                  <Draggable key={task} draggableId={task} index={index}>
                    {(provided) => (
                      <div
                        ref={provided.innerRef}
                        {...provided.draggableProps}
                        {...provided.dragHandleProps}
                        className="mb-2 p-4 bg-white rounded-lg shadow-sm border border-gray-200"
                      >
                        {task}
                      </div>
                    )}
                  </Draggable>
                ))}
                {provided.placeholder}
              </div>
            )}
          </Droppable>
        </div>
      </DragDropContext>
      <button 
        onClick={handleSaveTasks}
        className="px-6 py-3 bg-blue-600 text-white rounded-lg shadow mt-6 hover:bg-blue-700 transition"
      >
         
      </button>
    </div>
  );
};

export default Mission;
