// Initialize Firebase
const app = initializeApp(firebaseConfig);
const db = getFirestore(app);

// Actions
export const logAction = async (userId, actionType) => {
  const actionRef = doc(collection(db, 'actions'));
  const actionData = {
    userId,
    actionType,
    timestamp: Timestamp.now(),
    date: new Date().toISOString().split('T')[0] // YYYY-MM-DD format
  };
  await setDoc(actionRef, actionData);
};

export const fetchActionHistory = async (userId) => {
  const actionsQuery = query(
    collection(db, 'actions'),
    where('userId', '==', userId)
  );
  
  const querySnapshot = await getDocs(actionsQuery);
  const actions = querySnapshot.docs.map((doc) => doc.data());
  
  return actions;
};

// Tasks
export const addTaskForUser = async (userId, tasks) => {
  const today = new Date().toISOString().split('T')[0]; // YYYY-MM-DD format
  const tasksRef = doc(collection(db, 'tasks', today, 'users'), userId);
  await setDoc(tasksRef, { tasks }, { merge: true });
};

export const fetchTasksForUser = async (userId) => {
  const today = new Date().toISOString().split('T')[0]; // YYYY-MM-DD format
  const tasksRef = doc(collection(db, 'tasks', today, 'users'), userId);
  const tasksSnap = await getDoc(tasksRef);

  if (tasksSnap.exists()) {
    return tasksSnap.data().tasks || [];
  } else {
    return [];
  }
};

// Users
export const fetchUsers = async () => {
  const usersRef = collection(db, 'users');
  const usersSnapshot = await getDocs(usersRef);
  const users = usersSnapshot.docs.map((doc) => ({
    id: doc.id,
    ...doc.data()
  }));
  return users;
};

// Fetch user details
export const fetchUserDetails = async (name) => {
  try {
    const userRef = doc(db, 'users', name); // Adjusted path to 'users'
    const userSnap = await getDoc(userRef);

    if (userSnap.exists()) {
      return userSnap.data();
    } else {
      throw new Error('User not found');
    }
  } catch (error) {
    console.error("Error fetching user details: ", error);
    throw error; // Re-throw the error for handling in the component
  }
};

// Update tasks for a user
export const updateTaskForUser = async (userId, task, taskIndex) => {
  try {
    const today = new Date().toISOString().split('T')[0]; // YYYY-MM-DD format
    const userTasksRef = doc(collection(db, 'tasks', today, 'users'), userId);
    const userTasksSnap = await getDoc(userTasksRef);

    if (userTasksSnap.exists()) {
      const userData = userTasksSnap.data();
      const tasks = userData.tasks || [];
      tasks[taskIndex] = task; // Update the task at the specified index
      await updateDoc(userTasksRef, { tasks }); // Update the document in the database
    } else {
      throw new Error('User document does not exist');
    }
  } catch (error) {
    console.error('Error updating task:', error);
  }
};

// Log daily attendance
export const logDailyAttendance = async (userId, attendanceType) => {
  const today = new Date().toISOString().split('T')[0]; // YYYY-MM-DD format
  const attendanceRef = doc(collection(db, 'attendance', today, 'users'), userId);
  const attendanceData = {
    userId,
    attendanceType,
    timestamp: Timestamp.now(),
  };
  await setDoc(attendanceRef, attendanceData);
};

// Fetch daily attendance
export const fetchDailyAttendance = async (userId, date) => {
  const attendanceRef = doc(collection(db, 'attendance', date, 'users'), userId);
  const attendanceSnap = await getDoc(attendanceRef);

  if (attendanceSnap.exists()) {
    return attendanceSnap.data();
  } else {
    throw new Error('Attendance record not found');
  }
};

// Fetch monthly attendance
export const fetchMonthlyAttendance = async (userId, year, month) => {
  const startDate = new Date(year, month - 1, 1).toISOString().split('T')[0]; //    
  const endDate = new Date(year, month, 0).toISOString().split('T')[0]; //    

  const attendanceCollectionRef = collection(db, 'attendance');
  const attendanceQuery = query(
    attendanceCollectionRef,
    where('timestamp', '>=', new Date(startDate).toISOString()),
    where('timestamp', '<=', new Date(endDate).toISOString())
  );

  const querySnapshot = await getDocs(attendanceQuery);
  const attendanceRecords = querySnapshot.docs
    .filter(doc => doc.data().userId === userId)
    .map(doc => doc.data());

  return attendanceRecords;
};

// Fetch monthly tasks
export const fetchMonthlyTasks = async (userId, year, month) => {
  const startDate = new Date(year, month - 1, 1).toISOString().split('T')[0]; //    
  const endDate = new Date(year, month, 0).toISOString().split('T')[0]; //    

  const allTasks = [];
  let currentDate = new Date(startDate);

  while (currentDate <= new Date(endDate)) {
    const dateStr = currentDate.toISOString().split('T')[0];
    const tasksRef = doc(collection(db, 'tasks', dateStr, 'users'), userId);
    const tasksSnap = await getDoc(tasksRef);

    if (tasksSnap.exists()) {
      allTasks.push({ date: dateStr, tasks: tasksSnap.data().tasks });
    }

    currentDate.setDate(currentDate.getDate() + 1);
  }

  return allTasks;
};
